.App {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
    background-color: #181824;
  }
  
  .App_todo {
    height: 28rem;
    width: 28rem;
  }
  
  .App_input_wrapper{
    display: flex;
    align-items: center;
    width: 100%;
    height: 2.5rem;
     background-color: #25273c;
     border-radius: 15px;
     overflow: hidden;
  }
  
  .App_input {
    flex: 1;
    outline: none;
    border: none;
    padding-right: 1rem;
    padding-left: 1rem;
    font-size: 1rem;
    background-color: transparent;
    color: white;
  }
  
  .App_input_button{
    width: 3rem;
    height: 100%;
    background-color: #4ec032;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    cursor: pointer;
  }
  
  .App_todo_list{
    background-color: #25273c;
    height: 100%;
    margin-top: 2rem;
    overflow: auto;
  }
  
  .empty{
    color: white;
    text-align: center;
    margin-top: 6rem;
    font-size: 1.5rem;
  }